import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';
import { IBanco } from '../models/banco';

@Injectable()
export class BancosService {
  constructor(private http: HttpClient) {}
  /**
   * Funcion en el servicio
   * - Esta funcion retorna los bancos activos.
   * @returns Bancos activos
   */
  getBancos(filter?:any): Observable<IBanco[]> {
    let f = '?';
    f += filter == 'desc' ? 'sort=-descripcion' : 'sort=descripcion'; 
    
    return this.http.get<IBanco[]>(`${environment.administracion.bancos}${f}`);
  }
/**
 * Funcion en el Servicio 
 * @param banco 
 * @returns Un banco nuevo
 */
  saveBanco(banco: IBanco): Observable<IBanco> {
    return this.http.post<IBanco>(`${environment.administracion.bancos}`, banco);
  }
/**
 * Funcion en el servicio
 * @param banco 
 * @returns Banco actualizado
 */
  updateBanco(banco: IBanco): Observable<IBanco> {
    return this.http.put<IBanco>(`${environment.administracion.bancos}`+'/'+banco.idbanco, banco);
  }
/**
 * Funcion en el servicio
 * @param idbanco 
 * @returns Elimina el banco
 */
  deleteBanco(idbanco: number): Observable<IBanco> {
    return this.http.delete<IBanco>(environment.administracion.bancos+'/'+idbanco);
  }

}